<script setup>

const isHolding = ref(false)

const distance = ref('200px')
const radius = ref('3rem')

function holding(ledpointed){
  console.log('holding!' + ledpointed);
  document.getElementById(ledpointed + 'options').classList.remove('hidden');
}
function release(ledpointed){
  console.log('realising!');
  document.getElementById(ledpointed + 'options').classList.add('hidden');
}

async function changeColor(lednumber, color){
  console.log('The led numer ' + lednumber + ' color has changed to ' + color);
  const change = await useFetch('http://192.168.1.94:3000/changeColor/'+ lednumber + '/' + color)
}

</script>


<style scoped>
  .optionCircle{
    border-radius: 100vw;
    position: absolute;
    width: 6rem;
    height: 6em;
    color: white;
    user-select: none;
  }
  .optionCircle:hover{
    cursor: pointer;
  }

  .deg36{
    bottom: calc(200px * sin(36deg) - 3rem);
    left: calc(200px * cos(36deg) - 3rem);
  }
  .deg72{
    bottom: calc(200px * sin(72deg) - 3rem);
    left: calc(200px * cos(72deg) - 3rem);
  }
  .deg108{
